<template>
  <div class="set-up">
    <div class="set-up-header">
      <div class="header-left" @click="SwitchToUser">
        <span class="iconfont icon-arrowleft"></span>
      </div>
      <div class="header-right">设置</div>
    </div>
    <div class="set-up-body">
      <div class="body-container">
        <div class="container-item" v-for="item in WordText" :key="item.type">
          <div class="i-m">{{ item.text }}</div>
          <div class="i-icon">
            <span class="iconfont icon-arrowright"></span>
          </div>
        </div>
        <div class="login-item">
          <div
            class="container-login"
            v-if="!loginState"
            @click="SwitchLoginPage"
          >
            未登录
          </div>
          <div class="login-state" v-if="loginState">
            <div class="taggle-account" @click="JumpToLogin">切换账号</div>
            <div class="logout-button" @click="logoutEvent">退出登录</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.set-up {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background: black;

  .set-up-header {
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid gray;

    .header-left {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;

      .icon-arrowleft {
        font-size: 20px;
      }
    }

    .header-right {
      flex: 1;
      text-align: center;
      height: 50px;
      line-height: 50px;
      padding-right: 25px;
    }
  }

  .body-container {
    width: 100%;
    font-size: 15px;
    .container-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 40px;
      padding: 0px 20px;
      border-bottom: 1px solid gray;
    }
    .login-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 50px;

      .container-login,
      .taggle-account,
      .logout-button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 220px;
        height: 40px;
        border: 1px solid gray;
      }
      .taggle-account {
        margin-bottom: 20px;
      }
    }
  }
}
</style>
<script>
import { mapState,mapMutations } from "vuex";
export default {
  data() {
    return {
      WordText: [
        {
          type: "message",
          text: "账号资料",
        },
        {
          type: "protocol",
          text: "用户协议",
        },
        {
          type: "policy",
          text: "隐私政策",
        },
        {
          type: "other",
          text: "关于我们",
        },
      ],
      loginState: false,
    };
  },
  created(){
    this.initStorageState();
  },
  methods: {
    SwitchLoginPage() {
      this.$router.push({
        path: "/login",
      });
    },
    SwitchToUser() {
      this.$router.go(-1);
    },
    initStorageState() {
      if (localStorage.user) {
        let currentStorage = JSON.parse(localStorage.user);
        if (currentStorage[this.telephoneString]) {
          if (currentStorage[this.telephoneString].isRun) {
            this.loginState = true;
          } else {
            this.loginState = false;
          }
        } else {
          this.loginState = false;
        }
      } else {
        this.loginState = false;
      }
    },
    JumpToLogin(){
        this.$router.push({
            path:'/login'
        })
    },
    logoutEvent(){
        let currentStr=JSON.parse(localStorage.user);
        let obj={}
        obj[this.telephoneString]={
            ...currentStr[this.telephoneString],isRun:false
        }
        localStorage.user=JSON.stringify({...currentStr,...obj});
        this.loginState=false;
        this.modifyTelephone('');
        this.modifyUserState(false);
        this.modifyUserImage('');
        this.$toast.success('退出成功');
        this.$router.push({
            path:'/'
        });

    },
    ...mapMutations(['modifyTelephone','modifyUserState','modifyUserImage'])
  },
  computed: {
    ...mapState(["telephoneString"]),
  },
};
</script>